// -----------------------------------------------------------------------------
// This file contains color & theming variables for reveal.
//
// This file should be imported after the main theme abstracts/_colors.scss
// because it relies on color variables defined there.
// -----------------------------------------------------------------------------

/// Theme to use for normal slides
/// @type String
$reveal-slide-theme: "light" !default;

/// Theme to use for title slides
/// @type String
$reveal-title-theme: "light" !default;

/// Theme to use for divider slides
/// @type String
$reveal-divider-theme: "primary" !default;

/// Reveal background colours mapped by theme
/// @prop {String} keys - Identifiers for the theme (light, dark, primary)
/// @prop {Map} values - Values are background colours for each theme
/* prettier-ignore */
$reveal-bg-color: (
  "light": #{$white},
  "dark": #{$black},
  "primary": #{$primary-color},
);

/// Reveal body text colours mapped by theme
/// @prop {String} keys - Identifiers for the theme (light, dark, primary)
/// @prop {Map} values - Values are body text colours for each theme
/* prettier-ignore */
$reveal-body-color: (
  "light": #{$text-color},
  "dark": #{$text-color-inverted},
  "primary": #{$text-color},
);

/// Reveal headline text colours mapped by theme
/// @prop {String} keys - Identifiers for the theme (light, dark, primary)
/// @prop {Map} values - Values are headline text colours for each theme
/* prettier-ignore */
$reveal-headline-color: (
  "light": #{$primary-color},
  "dark": #{$primary-color},
  "primary": #{$text-color},
);
