// -----------------------------------------------------------------------------
// This file contains basic styles related to buttons.
//
// It also provides the following utility classes:
//    .button - add to any button-like element (e.g. button, link, input[type="submit"] to turn into a default black button
//    .button--outline - add to any button-like element to turn into an outline button
// -----------------------------------------------------------------------------

%button {
  @include text-component;
  padding-left: $space-md;
  padding-right: $space-md;
  text-decoration: none;
  appearance: none;
  border-radius: $border-radius;
  cursor: pointer;
  border: 1px solid $black;
  background-color: $black;
  color: $text-color-inverted;
  @include transition("sm", border-color, background-color, color, box-shadow);

  @include on-event {
    border-color: $primary-color;
    background-color: $primary-color;
    color: $black;
    box-shadow: 0 0 8px $box-shadow-color;
  }
}

.button,
/* a element selector needed for specificity to overwrite base a styles */
a.button,
button,
input[type="submit"] {
  /* Apply default black button styles to all button-like elements */
  @extend %button;
}

.button--outline,
/* a element selector needed for specificity to overwrite base a styles */
a.button--outline {
  @extend %button;
  /* Override button properties for outline button */
  border-color: $text-color;
  background-color: transparent;
  color: $text-color;
}
