// -----------------------------------------------------------------------------
// This file contains basic styles related to forms and form elements.
//
// It also provides the following utility classes:
//    .form - add to a container to apply form styles
//    .form-control - add to a container to add form control grouping styles
//    .no-label - add to input elements that don't have a label to center placeholder text
// -----------------------------------------------------------------------------

%text-input {
  @include text-component;
  appearance: none;
  padding: $space-sm;
  display: block;
  height: $space-clickable;
  // Useful if width is ever overridden
  min-height: $space-clickable;
  width: 100%;
  // Useful if width is ever overridden
  max-width: 100%;
  border-radius: $border-radius;
  border: 1px solid $component-border-color;
  background-color: transparent;
  @include transition("sm", border-color, background-color);

  &:disabled {
    background-color: $component-disabled-bg-color;
    color: $component-disabled-text-color;
    cursor: not-allowed;
  }

  // Apply hover, focus, active state if input is not disabled
  &:not(:disabled) {
    @include on-event {
      border-color: $primary-color;
      background-color: $primary-bg-color;
    }
  }

  // Apply invalid state once input is blurred (so it doesn't turn red as you're typing)
  &:not(:focus):invalid {
    border-color: $error-color;
    background-color: $error-bg-color;
    color: $error-color;
  }
}

form,
.form {
  border-top: 1px solid $primary-color;
  padding: $space-vertical 0;

  .form-control {
    margin: $space-sm 0;
    position: relative;
  }

  input[type="text"],
  input[type="email"],
  select:not([multiple]) {
    @extend %text-input;
    /* Offset the top and bottom padding so there's space for the label */
    padding-top: $space-unit * 2.5; // 20px
    padding-bottom: $space-xs; // 4px

    /* If there is no label then vertically center the text */
    // Last child works if there's nothing else after the input
    &:last-child,
    // Class for when this isn't the case
    &.no-label {
      padding: $space-sm;
    }

    /* Assumes label is placed underneath the input */
    /* Initially show the label on top of the input like placeholder text */
    & ~ label {
      position: absolute;
      top: $space-sm;
      left: $space-sm;
      color: $component-text-color;
      pointer-events: none;
      @include transition("short", transform);
    }

    /* Make label smaller and move up to make space for text */
    &:focus ~ label,
    &:not(:placeholder-shown) ~ label {
      transform: scale(0.8125) translateY(-1 * $space-sm);
      transform-origin: center left;
    }
  }

  input[type="search"] {
    @extend %text-input;
    /* Override the style in normalize.css */
    outline-offset: 0;
  }
}
