// -----------------------------------------------------------------------------
// This file contains basic styles related to icons
//
// It also provides the following utility classes:
//    .icon-link - add to links that only (visibly) contain icons to style as an icon link
// -----------------------------------------------------------------------------

/* likely that the ANU icons will be used as "images", so width: 100% seems like a sensible default */
svg.anu-icon {
  width: 100%;
}

/* some fontawesome hacks */
svg.icon {
  width: 1em;
  height: 1em;
  vertical-align: -0.125em;
  fill: currentColor;
}

/**
* For icons that act as standalone links / buttons
* 1. Makes the icon a standard 16 x 16px size
* 2. Centers it in a 48 x 48px clickable area
* 3. Make the icon turn gold when hover / focus / active
*/
.icon-link {
  font-size: $space-unit * 2; // 16px
  display: inline-block;
  width: $space-unit * 6; // 48px
  height: $space-unit * 6; // 48px
  padding: $space-unit * 2; // 16px
  @include transition("sm", color);

  & svg.icon {
    vertical-align: top;
  }

  @include on-event {
    color: $link-color;
  }
}
