// -----------------------------------------------------------------------------
// This file contains all styles related to the callout box component.
//
// It also provides the following utility classes:
//    .callout-box - style a container like a default grey callout box
//    .error-box - style a container like an error (red) callout box
//    .warn-box - style a container like an error (yellow) callout box
//    .info-box - style a container like an error (blue) callout box
// -----------------------------------------------------------------------------

%callout-box {
  padding: $space-md;
  /* Include solid stripe down left size of box */
  border-left: 4px solid $component-border-color;
  background-color: $component-bg-color;
  margin: $space-vertical 0;

  *:first-child {
    margin-top: 0;
  }

  *:last-child {
    margin-bottom: 0;
  }
}

.callout-box {
  @extend %callout-box;
}

.error-box {
  @extend %callout-box;
  border-color: $error-color;
  background-color: $error-bg-color;
}

.warn-box {
  @extend %callout-box;
  border-color: $warn-color;
  background: $warn-bg-color;
}

.info-box {
  @extend %callout-box;
  border-color: $info-color;
  background: $info-bg-color;
}
