// -----------------------------------------------------------------------------
// This file contains styles to respond to config classes set on slides
//
// It provides the following utility classes:
//    .hide-footer - set on a slide section to hide footer on that slide
//    .hide-progress - set on a slide section to hide progress bar on that slide
//    .hide-number - set on a slide section to hide slide number on that slide
// -----------------------------------------------------------------------------

.reveal-viewport.hide-footer {
  .footer {
    opacity: 0;
    visibility: hidden;
  }
}

.reveal-viewport.hide-progress {
  .reveal .progress {
    opacity: 0;
    visibility: hidden;
  }
}

.reveal-viewport.hide-number {
  .reveal .slide-number {
    opacity: 0;
    visibility: hidden;
  }
}
