// -----------------------------------------------------------------------------
// This file contains all application-wide Sass variables, except for named
// color variables, which live in abstracts/_colors.scss.
// -----------------------------------------------------------------------------

/// Regular font family
/// @type List
$text-font-stack: "Public Sans", "Helvetica Neue Light", "Helvetica Neue",
  "Helvetica", "Arial", sans-serif !default;

/// Code (monospace) font family
/// @type List
$code-font-stack: "Roboto Mono", "Courier New", "DejaVu Sans Mono",
  "Bitstream Vera Sans Mono", "Monaco", monospace !default;

/// Base colors for the theme
/// Do not use this map to set colors, use color variables defined in abstracts/_colors.scss
/// @prop {String} keys - Keys are color names mapped to given colors
/// @prop {Map} values - Values are colors
$base-colors: (
  "white": #ffffff,
  "anu-gold": #be8303,
  "black": #000000,
  "copper": #be4e0e,
  "teal": #0085ad,
) !default;

/// Color interval to generate tints from base colors
/// @type Number
$color-interval: 22% !default;

/// Opacities map
/// @prop {String} keys - Keys are identifiers mapped to a given opacity value
/// @prop {Map} values - Values are opacity value
/* prettier-ignore */
$opacities: (
  'base': 1,      // base color, no transparency applied
  'dark': 0.78,    // opacity used for a dark shadow
  'medium': 0.56, // opacity used for a medium shadow
  'light': 0.34,  // opacity used for a light shadow
  'lightest': 0.12,  // opacity used for the lightest shadow
) !default;

/// Base spacing unit - edit this to change scale for whole site
/// @type Length
$space-unit: 0.5rem !default; // 8px based on browser default 1rem = 16px

/// Extra small spacing (e.g. for icon and text in a button)
/// @type Length
$space-xs: $space-unit * 0.5; // 4px

/// Small spacing (e.g. for padding inside small "piece" components like buttons)
/// @type Length
$space-sm: $space-unit * 1.5; // 12px

/// Medium spacing (e.g. for padding inside medium "pattern" components like cards)
/// @type Length
$space-md: $space-unit * 3; // 24px

/// Large spacing (e.g. for spacing between unrelated elements)
/// @type Length
$space-lg: $space-unit * 6; // 48px

/// Extra large spacing (e.g. for padding inside large containers like banner or page content)
/// @type Length
$space-xl: $space-unit * 12; // 96px

/// Vertical spacing (e.g. default line height, spacing between components)
/// @type Length
$space-vertical: $space-md; // 24px

/// Text spacing (e.g. between headings and paragraphs)
/// @type Length
$space-text: $space-unit * 2; // 16px

/// Spacing for a clickable area which should be min 48 x 48px
/// @type Length
$space-clickable: $space-unit * 6; // 48px

/// Font sizes map - keys should be the same as in $line-heights map
/// @prop {String} keys - Keys are identifiers mapped to a given font-size
/// @prop {Map} values - Values are font-sizes expressed in rem
/* prettier-ignore */
$font-sizes: (
  'title': 8 * $space-unit,       // 64px
  'h1': 4.625 * $space-unit,      // 37px
  'h2': 3.5 * $space-unit,        // 28px
  'h3': 2.625 * $space-unit,      // 21px
  'h4': 2 * $space-unit,          // 16px
  'h5': 1.625 * $space-unit,      // 13px
  'h6': 1.375 * $space-unit,      // 11px
  'lead': 2.625 * $space-unit,    // 21px
  'body': 2 * $space-unit,        // 16px
  'component': 2 * $space-unit,   // 16px
  'caption': 1.75 * $space-unit,  // 14px
  'mini': 1.375 * $space-unit,    // 11px
  'micro': 0.75 * $space-unit,    // 6px
) !default;

/// Line heights map - keys should be the same as in $font-sizes map
/// Line height values should be multiples of the base space unit for vertical flow
/// @prop {String} keys - Keys are identifiers mapped to a given line-height
/// @prop {Map} values - Values are line-heights expressed in rem
/* prettier-ignore */
$line-heights: (
  'title': 9 * $space-unit,       // 72px
  'h1': 5 * $space-unit,          // 40px
  'h2': 4 * $space-unit,          // 32px
  'h3': 3 * $space-unit,          // 24px
  'h4': 2 * $space-unit,          // 16px
  'h5': 2 * $space-unit,          // 16px
  'h6': 2 * $space-unit,          // 16px
  'lead': 4 * $space-unit,        // 32px
  'body': 3 * $space-unit,        // 24px
  'component': 2 * $space-unit,   // 16px
  'caption': 2 * $space-unit,     // 16px
  'mini': 2 * $space-unit,        // 16px
  'micro': $space-unit,           // 8px
) !default;

/// Breakpoints map
/// @prop {String} keys - Keys are identifiers mapped to a given length
/// @prop {Map} values - Values are actual breakpoints expressed in pixels
$breakpoints: (
  "sm": 544px,
  "md": 768px,
  "lg": 992px,
  "xl": 1170px,
) !default;

/// Container's maximum width
/// @type Length
$off-canvas-menu-width: 320px !default;

/// Container's maximum width
/// @type Length
$container-max-width: calc(1170px - #{8 * $space-unit}) !default;

/// Spacing between grid columns
/// @type Length
$grid-column-gap: $space-unit * 4; // 32px

/// Default border radius
/// @type Length
$border-radius: 4px !default;

/// Transition durations map
/// @prop {String} keys - Identifiers for the size of the component being transitioned
/// @prop {Map} values - Values are transition-duration values
$transition-durations: (
  "sm": 150ms,
  "md": 300ms,
  "lg": 450ms,
);

/// Default transition timing function
/// @type String
$transition-timing-function: ease !default;

/// Default transition delay
/// @type Length
$transition-delay: 50ms !default;

/// Z-index map
/// @prop {String} keys - Keys are component identifiers mapped to a given z-index
/// @prop {Map} values - Values are z-index values
$z-index: (
  search-menu: 210,
  search-menu-overlay: 200,
  skip-to-main: 150,
  nav-menu: 110,
  nav-menu-overlay: 100,
  header-logo: 10,
  logo-bg: -1,
);
