// -----------------------------------------------------------------------------
// This file contains all styles related to tables.
//
// It also provides the following utility classes:
//    .striped - add to table elements to give even columns light gray background
//    .highlight - add to tr elements to highlight the row in gold
// -----------------------------------------------------------------------------

table {
  border-collapse: collapse;
  width: 100%;
  margin-top: $space-vertical;
  margin-bottom: $space-vertical;

  &.striped {
    th,
    td {
      &:nth-child(even) {
        background-color: $component-bg-color;
      }
    }
  }
}

thead {
  /* Not specifying border color makes it inherit from text colour, which is good for theming */
  border-bottom: 2px solid;
}

tr {
  /* Not specifying border color makes it inherit from text colour, which is good for theming */
  border-bottom: 1px solid;

  &.highlight {
    td,
    th {
      /* important used to override .striped style specificity from :nth-child */
      background-color: $primary-color !important;
      /* if background is gold then text should be black */
      color: $text-color;
    }
  }
}

th {
  @include sub-heading;
}

td,
th {
  padding: $space-xs;
  vertical-align: top;
  text-align: left;
}
