// -----------------------------------------------------------------------------
// This file contains basic styles related to typography.
//
// It also provides the following utility classes:
//    .impact - add to any h1 heading to style like impact headline (all caps)
//    .lead - add to any paragraph to style as a larger lead paragraph
// -----------------------------------------------------------------------------

body {
  @include body-copy;
  @include text-size("body");
  color: $text-color;
  font-family: $text-font-stack;
}

h1 {
  @include headline;
  @include text-size("h1");

  &.impact {
    @include headline($impact: true);
  }
}

h2,
h3,
h4,
h5,
h6 {
  @include sub-heading;
}

h2 {
  @include text-size("h2");
}

h3 {
  @include text-size("h3");
}

h4 {
  @include text-size("h4");
}

h5 {
  @include text-size("h5");
}

h6 {
  @include text-size("h6");
}

p {
  &.lead {
    @include text-size("lead");
  }
}

b,
strong {
  font-weight: 600;
}

code,
kbd,
samp {
  font-family: $code-font-stack;
  display: inline-block;
  padding: $space-xs; // 4px
  border-radius: 2px;
  text-decoration: inherit;
}

code {
  background-color: $primary-bg-color;
  color: $component-text-color;
}

kbd {
  background-color: $component-bg-color;
  box-shadow: 0 1px 1px 0 $box-shadow-color,
    /* Use inset box-shadow for the border so it doesn't mess with sizing which is controlled with line-height */
      inset 0 0 0 1px $component-border-color;
  white-space: nowrap;
}

blockquote {
  /* Not specifying the border color makes it inherit from the text color, which is good for theming */
  border-left: 1px solid;
  padding: $space-sm $space-md;

  > p {
    @include headline;
    @include text-size("lead");
    color: $primary-color;
    margin: 0;
  }

  em:last-child {
    @include sub-heading;
    @include text-size("caption");
    color: $text-color;
    display: block;
    margin-top: $space-vertical;
  }
}

/**
* Text spacing
*/
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: $space-vertical;
  margin-bottom: $space-text;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

p,
ol,
ul {
  margin-top: $space-text;
  margin-bottom: $space-text;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

ol,
ul {
  ol,
  ul {
    margin-top: 0;
    margin-bottom: 0;
  }
}
