// -----------------------------------------------------------------------------
// This file generates the CSS stylesheet that gets included in the reveal
// layout when page theme is set to dark.
// -----------------------------------------------------------------------------

// 0. Hook for overriding default theme variables
@import "custom-variables";

$reveal-slide-theme: "dark" !default;
$reveal-title-theme: "dark" !default;
$reveal-divider-theme: "primary" !default;

/// Relative or absolute URL where all assets are served from
/// @type String
/// @example scss - When using a CDN
///   $base-url: 'http://cdn.example.com/assets/';
$base-url: "/assets/" !default;

@import "reveal";
