// -----------------------------------------------------------------------------
// This file contains all styles related to the divider slide layout
// -----------------------------------------------------------------------------

/* prettier-ignore */
$reveal-divider-slide-bg-color: map-get($reveal-bg-color, $reveal-divider-theme);
/* prettier-ignore */
$reveal-divider-slide-body-color: map-get($reveal-body-color, $reveal-divider-theme);
/* prettier-ignore */
$reveal-divider-slide-headline-color: map-get($reveal-headline-color, $reveal-divider-theme);

/* We use nested selector so only the slide gets selected and not the .reveal
container which inherits the slide classes and to override specificity */
.reveal .slides > section.divider-slide {
  color: $reveal-divider-slide-body-color;
  grid-template-areas: "num num num gap hdl hdl hdl hdl hdl hdl hdl hdl";

  h1 {
    @include text-size("title");
    @include headline($impact: true);
    color: $reveal-divider-slide-headline-color;
    grid-area: hdl;
    margin-top: 0;
  }

  .number {
    @include text-size("title");
    @include headline($impact: true);
    grid-area: num;
  }
}

.reveal .backgrounds > .slide-background.divider-slide {
  background-color: $reveal-divider-slide-bg-color;
}
