// -----------------------------------------------------------------------------
// This file contains very basic styles.
// -----------------------------------------------------------------------------

/**
 * Set up a decent box model on the root element
 */
html {
  box-sizing: border-box;
}

/* Make body a flex element so short pages can fill vertical screen space */
body {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

/**
 * Make all elements from the DOM inherit from the parent box-sizing
 * Since `*` has a specificity of 0, it does not override the `html` value
 * making all elements inheriting from the root box-sizing value
 * See: https://css-tricks.com/inheriting-box-sizing-probably-slightly-better-best-practice/
 */
*,
*::before,
*::after {
  box-sizing: inherit;
}

/**
 * Remove styles for links as a base
 */
a,
a:visited {
  color: inherit;
  text-decoration: none;
}

/**
* Styles for text links
*/
a {
  text-decoration: underline;
  // Allow breaks inside so long urls & emails don't overflow
  word-break: break-word;
  @include transition("sm", color);

  @include on-event {
    color: $link-color;
  }
}

hr {
  margin: $space-vertical 0;
}
