// -----------------------------------------------------------------------------
// Base styles for ANU theme for reveal.js - overriding default reveal.js styles
// and providing base slide layouts
// -----------------------------------------------------------------------------

.reveal-viewport {
  /* Override body.reveal-viewport bg color and set to the slide theme bg color
  so there's not an awkward line when slides transition */
  background-color: map-get($reveal-bg-color, $reveal-slide-theme);
}

.reveal {
  /* Place slide number in bottom-left corner (fake being part of the footer) */
  .slide-number {
    @include text-size("micro");
    @include headline();
    bottom: $slide-padding - $slide-footer-height;
    left: $slide-padding;
    padding: 0;
    width: $slide-footer-height;
    height: $slide-footer-height;
    line-height: $slide-footer-height;
    background: none;
    color: map-get($reveal-body-color, $reveal-slide-theme);
    text-align: center;
    @include transition("sm", opacity, visibility);

    /* Override base a styles */
    a {
      text-decoration: none;
    }
  }

  .has-dark-background .slide-number {
    color: map-get($reveal-body-color, "dark");
  }

  .has-light-background .slide-number {
    color: map-get($reveal-body-color, "dark");
  }

  /* Progress bar */
  .progress {
    color: $primary-color;
    height: $slide-progress-height;
    top: 0;
    @include transition("sm", opacity, visibility);
  }

  /* Each section element in .slides container is a slide */
  .slides > section {
    /* Set base text properties to 'body' so inherit works */
    @include text-size("body");
    /* Make text left-aligned to override reveal styles */
    text-align: left;
    /* Padding around the edges of the slide */
    padding: $slide-padding;
    /* Setup 12-column grid that all slide-layouts & special slide types use */
    display: grid !important;
    grid-template-columns: repeat(12, $slide-column-width);
    grid-column-gap: 0;
    grid-row-gap: $space-text;
  }
}
