// -----------------------------------------------------------------------------
// This file is like `main.scss` for reveal - it imports all styles needed for
// reveal layouts. It should not contain anything but `@import` and comments.
// -----------------------------------------------------------------------------

@charset "UTF-8";

// 1. Configuration and helpers
@import
  // Reveal variables needs to come before main theme variables because we override the base space unit
  'reveal/variables',
  'abstracts/variables',
  'abstracts/functions',
  'abstracts/colors',
  // Reveal colors needs to come after main theme colors because we use color variables defined by main theme
  'reveal/colors',
  'abstracts/mixins';
  
// 2. Vendors
@import
  'vendors/normalize';

// 3. Base stuff
@import
  'base/base',
  'base/button',
  'base/form',
  'base/grid',
  'base/helpers',
  'base/icons',
  'base/images',
  'base/typography',
  'base/table',
  'reveal/base';

// 4. Partials generate parts of a page
@import
  'reveal/divider-slide',
  'reveal/footer',
  'reveal/slide-layouts',
  'reveal/title-slide';

// 5. Components
@import
  'components/callout-box',
  'components/card',
  'components/collection-listing',
  'components/filter-buttons',
  'components/segmented-control',
  'components/select-control',
  'components/select-typeahead',
  'components/tag';

// 6. Page-specific styles
@import
  'reveal/slide-options';

