// -----------------------------------------------------------------------------
// This file contains all styles related to the reveal footer
// -----------------------------------------------------------------------------

.footer {
  @include text-size("micro");
  @include headline(true);
  height: $slide-footer-height;
  align-items: center;
  position: absolute;
  bottom: $slide-padding - $slide-footer-height;
  right: $slide-padding;
  /* Use 11 of 12 columns but calculate using the viewport because footer doesn't get scaled */
  width: calc((100vw - #{2 * $slide-padding}) * 11 / 12);
  display: flex;
  justify-content: space-between;
  z-index: 11;
  @include transition("sm", opacity, visibility, color);
}

/* Make reveal footer text color white when background is dark */
.has-dark-background + .footer {
  color: $text-color-inverted;
}
