// -----------------------------------------------------------------------------
// Reveal specific variables. This is imported before the site variables so
// can also be used to provide custom theme variables.
//
// None of these are default variables, and so they can't be overridden by
// variables set in `custom-variable.scss`.
// -----------------------------------------------------------------------------

/*
* Override base spacing unit so everything is a larger scale for reveal pages
* We don't add !default so users can't override it in `custom-variables.scss`
*/
$space-unit: 1.25rem;

/// Slide width
/// @type Length
$slide-width: 120rem; // 1920px

/// Slide height
/// @type Length
$slide-height: 67.5rem; // 1080px

/// Slide padding - around content
/// @type Length
$slide-padding: 3rem; // 48px

/// Slide column width - calculate required width for 12 column layout
/// @type Length
$slide-column-width: ($slide-width - 2 * $slide-padding) / 12;

/// Slide footer height
/// @type Length
$slide-footer-height: 2rem; // 32px

/// Slide progress bar height
/// @type Length
$slide-progress-height: 0.5rem; // 8px
