// -----------------------------------------------------------------------------
// This file contains all styles related to the select form control component.
// -----------------------------------------------------------------------------

.select-control {
  select {
    /* The select element inherits styles from shared form styles in _form.scss */
    cursor: pointer;
    /* Larger right padding to make space for dropdown caret for single selects */
    padding-right: $space-md;

    @-moz-document url-prefix() {
      text-indent: -2px;
      line-height: inherit;
    }
  }

  /* Hide default caret in IE, appearance none  */
  &::-ms-expand {
    display: none;
  }

  /* Show custom dropdown caret for single selects */
  position: relative;

  &:after {
    content: "";
    width: 0.8em;
    height: 0.5em;
    background-color: $control-indicator-color;
    clip-path: polygon(100% 0%, 0 0%, 50% 100%);
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(-100%, -50%);
  }
}
