// -----------------------------------------------------------------------------
// This file contains all styles related to the tag component.
// -----------------------------------------------------------------------------

.tag {
  @include text-component;
  @include text-size("caption");
  background-color: $black;
  color: $text-color-inverted;
  text-decoration: none;
  @include transition("sm", background-color, color);
}

a.tag {
  // Needed to override specificity of base a styles
  color: $text-color-inverted;

  @include on-event {
    background-color: $primary-color;
    color: $text-color;
  }

  &.current-page {
    background-color: $primary-color;
    color: $text-color;
  }
}
