// -----------------------------------------------------------------------------
// This file contains all application-wide Sass functions.
// -----------------------------------------------------------------------------

/// Native `url(..)` function wrapper
/// @param {String} $base - base URL for the asset
/// @param {String} $type - asset type folder (e.g. `fonts/`)
/// @param {String} $path - asset path
/// @return {Url}
@function asset($base, $type, $path) {
  @return url($base + $type + $path);
}

/// Returns URL to an image based on its path
/// @param {String} $path - image path
/// @param {String} $base [$base-url] - base URL
/// @return {Url}
/// @require $base-url
@function image($path, $base: $base-url) {
  @return asset($base, "images/", $path);
}

/// Returns URL to a primary logo based on theme - use when 250px and wider
/// @param {String} $theme [light] - "light" or "dark"
/// @return {Url}
@function primary-logo($theme: "light") {
  // Set the logo color to gold-black for light theme by default
  $logo-color: "gold-black";
  @if $theme == "dark" {
    $logo-color: "gold-white";
  }

  @return image("logos/anu/primary/horizontal/#{$logo-color}.svg");
}

/// Returns URL to a secondary logo based on theme - use when 100-249px wide
/// @param {String} $theme [light] - "light" or "dark"
/// @return {Url}
@function secondary-logo($theme: "light") {
  // Set the logo color to gold-black for light theme by default
  $logo-color: "gold-black";
  @if $theme == "dark" {
    $logo-color: "gold-white";
  }

  @return image("logos/anu/secondary/horizontal/#{$logo-color}.svg");
}

/// Returns URL to a crest logo based on theme - use when 50 - 99px wide
/// @param {String} $theme [light] - "light" or "dark"
/// @return {Url}
@function crest-logo($theme: "light") {
  // Crest logo color is gold for both light and dark theme
  $logo-color: "gold";

  @return image("logos/anu/crest/outline/#{$logo-color}.svg");
}

/// Returns URL to a favicon logo based on theme - use when less than 50px wide
/// @param {String} $theme [light] - "light" or "dark"
/// @return {Url}
@function favicon-logo($theme: "light") {
  // Favicon logo color is gold for both light and dark theme
  $logo-color: "gold";

  @return image("logos/anu/favicon/outline/#{$logo-color}.svg");
}

/// Returns CSS color value for a color in the $base-colors palette
/// @param {String} $color-name - name of the color in $base-colors map
/// @return {String}
/// @require $base-colors
@function get-color($color-name) {
  // If the key exists in the map
  @if map-has-key($base-colors, $color-name) {
    // Return the color value
    @return map-get($base-colors, $color-name);
  }

  // If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no color value could be retrieved for `#{$color-name}`. "
        + "Available keys are: #{map-keys($base-colors)}.";
  }
}

/// Returns a color tinted to the specified level based on set color interval
/// @param {String} $color-name - name of the color in $base-colors map
/// @param {String?} $level [0] - color level (0, 1, 2, 3, 4) can go further but washes out to white
/// @return {String}
/// @require $color-interval
@function get-color-level($color-name, $level: 0) {
  $color: get-color($color-name);
  @return mix(#ffffff, $color, $level * $color-interval);
}

/// Returns CSS opacity value for a given key
/// @param {String} $key [base] - name of opacity level in $opacities map
/// @return {String}
/// @require $opacities
@function opacity($key) {
  // If the key exists in the map
  @if map-has-key($opacities, $key) {
    // Return the opacity value
    @return map-get($opacities, $key);
  }

  // If the key doesn't exist in the map
  @else {
    @warn "Unfortunately, no color value could be retrieved for `#{$key}`. "
        + "Available keys are: #{map-keys($opacities)}.";
  }
}

/// Returns a color with opacity set based on the given opacity level
/// @param {String} $color-name - name of the color in $base-colors map
/// @param {String?} $opacity-key ['base'] - opacity level name, default base is opacity of 100%
/// @return {String} color value with alpha
@function get-color-alpha($color-name, $opacity-key: "base") {
  $color: get-color($color-name);
  $opacity: opacity($opacity-key);

  // Use rgba() to manipulate the color's alpha level
  @return rgba($color, $opacity);
}
