// -----------------------------------------------------------------------------
// This file contains all styles related to the segmented form control component.
// -----------------------------------------------------------------------------

.segmented-control {
  font-size: inherit;
  display: flex;
  padding: 0;
  border: none;

  &__legend {
    @include body-copy;
    @include text-size("body");
    margin-bottom: $space-xs; // 4px
    flex: 0 0 auto;
    width: 100%;
  }

  &__input-label {
    @include text-component;
    text-align: center;
    flex: 1 1 auto;
    background-color: transparent;
    color: $text-color;
    border: 1px solid $component-border-color;
    border-right: none;
    cursor: pointer;
    @include transition("sm", background-color, color);

    &:first-of-type {
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 0;
    }

    &:last-of-type {
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 5px;
      border-top-left-radius: 0;
      border-top-right-radius: 5px;
      border-right: 1px solid $component-border-color;
    }
  }

  /* Hide the default browser-rendered radio button */
  &__input {
    left: -9999px;
    opacity: 0;
    position: absolute;
    visibility: none;
  }

  /* If the current object is clicked/tapped */
  input[type="radio"] {
    /* Make active/hover styles sit above checked/focus otherwise focus style won't apply when you click */
    &:active + label,
    &:hover + label {
      background-color: $primary-bg-color;
    }

    &:checked + label,
    &:focus + label {
      background-color: $primary-color;
    }
  }

  /* If the control has keyboard focus */
  input[type="radio"]:focus + label {
    @include unreal-focus;
  }
}
