// -----------------------------------------------------------------------------
// This file contains all styles related to the filter buttons component.
// -----------------------------------------------------------------------------

.filter-buttons {
  @include wrap-items;
  border: none;
  padding: 0;

  /* Hide the default browser-rendered radio input */
  &__radio {
    left: -9999px;
    opacity: 0;
    position: absolute;

    /* Make button look active if filter is active except the clear button */
    &:not(.filter-buttons__clear) {
      &:checked + label {
        background-color: $primary-color;
        border-color: $primary-color;
        color: $text-color;
      }
    }

    /* If the option has keyboard focus */
    &:focus + label {
      /* Show fake focus around label/button */
      @include unreal-focus;
      /* Make the button look active */
      background-color: $primary-color;
      border-color: $primary-color;
      color: $text-color;
    }
  }

  &__radio-label {
    text-align: center;
  }
}
