// -----------------------------------------------------------------------------
// This file contains all styles related to the title slide layout
// -----------------------------------------------------------------------------
/* prettier-ignore */
$reveal-title-slide-bg-color: map-get($reveal-bg-color, $reveal-title-theme);
/* prettier-ignore */
$reveal-title-slide-body-color: map-get($reveal-body-color, $reveal-title-theme);
/* prettier-ignore */
$reveal-title-slide-headline-color: map-get($reveal-headline-color, $reveal-title-theme);

/* We use nested selector so only the slide gets selected and not the .reveal
container which inherits the slide classes and to override specificity */
.reveal .slides > section.title-slide {
  color: $reveal-title-slide-body-color;
  height: 100%;

  /* Gold rule */
  &:before {
    content: "";
    width: 1px;
    position: absolute;
    /* Make rule super tall so it doesn't stop on tall pages */
    height: 1000%;
    top: -500%;
    background-color: $primary-color;
    left: $slide-padding + 9 * $slide-column-width;
  }

  /* ANU logo */
  &:after {
    box-sizing: content-box;
    content: "";
    width: 250px;
    height: 80px;
    transform: translateX(-48px);
    border-top: $space-unit solid $reveal-title-slide-bg-color;
    border-bottom: $space-unit solid $reveal-title-slide-bg-color;
    background-image: primary-logo($reveal-title-theme);
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
    bottom: $slide-padding * 3;
    left: $slide-padding + 9 * $slide-column-width;
    background-color: $reveal-title-slide-bg-color;
  }

  grid-template-areas: "main main main main main main main main gap asd asd asd";

  main {
    grid-area: main;
  }

  aside {
    @include text-size("mini");
    grid-area: asd;
    /* Custom amount to make container lineup with text in ANU logo */
    padding-left: 64px;
    /* Custom amount to give ANU logo enough clearance */
    padding-bottom: 196px;

    h3 {
      @include text-size("h6");
      margin-bottom: $space-xs;

      &:not(:first-child) {
        margin-top: $space-text;
      }
    }

    p {
      margin: 0;
    }

    ul {
      list-style: none;
      margin: 0;

      li {
        margin: $space-xs 0;
      }
    }
  }

  .title {
    @include text-size("title");
    @include headline($impact: true);
    color: $reveal-title-slide-headline-color;
  }
}

.reveal .backgrounds > .slide-background.title-slide {
  background-color: $reveal-title-slide-bg-color;
}
