// -----------------------------------------------------------------------------
// This file contains all styles related to the select typeahead form component.
// -----------------------------------------------------------------------------

.select-typeahead {
  input {
    /* The input element inherits styles from shared form styles in _form.scss */

    &[list] {
      /* Overwrite default right padding to make space for dropdown caret */
      padding-right: $space-clickable;

      /* Don't show the default caret in Chrome, it's got annoying behaviour */
      &::-webkit-calendar-picker-indicator {
        /* Doesn't work on Chrome 91+ without !important */
        display: none !important;
      }
    }
  }

  /* Clear input button styling */
  position: relative;

  &__clear {
    @extend .icon-link;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    background: none;
    border: none;
    color: $control-indicator-color;
    visibility: hidden;
    @include transition("sm", color, visibility);

    @include on-event {
      background: none;
      border: none;
      box-shadow: none;
      color: $component-text-color;
    }
  }

  /* Only show clear input button when it has text */
  input:not(:placeholder-shown) ~ &__clear {
    visibility: visible;
  }
}
