// -----------------------------------------------------------------------------
// This file contains CSS helper classes.
//
// It provides the following utility classes:
//    .clearfix - clear inner floats
//    .container - constrains content width & adds gold rule
//    .hide-text - hide text while making it readable for screen readers
//    .visually-hidden - hide element when not focussed or active while making it readable for screen readers
//    .hide-underline - hide underline text decoration, useful for links
// -----------------------------------------------------------------------------

/**
 * Clear inner floats
 */
.clearfix::after {
  clear: both;
  content: "";
  display: table;
}

/**
 * Content container with gold rule down the side
 */
.container {
  /* Center it in the viewport */
  margin: 0 auto; /* 1 */
  /* Leave 24px on either side */
  width: calc(100% - #{8 * $space-unit}); // 100% - 64px
  /* Set a max width for the container */
  max-width: $container-max-width;
  /* Add top and bottom padding, and left side so it sits away from gold rule */
  padding: $space-lg;
  /* Shrink right padding for more horizontal space, as there's no rule on right side */
  padding-right: 2 * $space-unit;

  /* No gold rule on small screens so we leave only the 24px margin on the sides */
  @include small-screens-only {
    width: calc(100% - #{6 * $space-unit}); // 100% - 48px
    padding: $space-lg 0;
  }

  /* Add ANU vertical gold rule down the left side on medium & large screens */
  position: relative;

  /* Use after so rule sits above any elements inside the container */
  &:after {
    content: "";
    width: 1px;
    background-color: $primary-color;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;

    /* Hide rule on small screens */
    @include small-screens-only {
      width: 0;
    }
  }
}

/**
 * Hide text while making it readable for screen readers
 * 1. Needed in WebKit-based browsers because of an implementation bug;
 *    See: https://code.google.com/p/chromium/issues/detail?id=457146
 */
.hide-text {
  overflow: hidden;
  padding: 0; /* 1 */
  text-indent: 101%;
  white-space: nowrap;
}

/**
 * Hide element while making it readable for screen readers
 * Element will become visible when focussed or active
 * Source: https://www.a11yproject.com/posts/2013-01-11-how-to-hide-content/
 */
.visually-hidden:not(:focus):not(:active) {
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  height: 1px;
  overflow: hidden;
  position: absolute;
  white-space: nowrap;
  width: 1px;
}

/**
 * Hide underline text decoration.
 * Useful for links that have inherited the underline style.
 */
.hide-underline {
  text-decoration: none;
}
