// -----------------------------------------------------------------------------
// This file contains basic styles related to images, figures, and figcaptions.
//
// It also provides the following utility classes:
//    .actual-size - add to an image to show at actual size rather than full width
//    .caption - add to a paragraph element to style like caption text.
// -----------------------------------------------------------------------------

img {
  max-width: 100%;
}

figure {
  margin: $space-vertical 0;
  position: relative;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }

  img {
    display: block;
    width: 100%;

    &.actual-size {
      width: auto;
      margin: auto;
    }
  }

  figcaption {
    @include text-size("caption");
    text-align: center;
    margin-top: $space-sm;
  }
}

/**
* Show images in markdown page content full width by default - page content gets
* put into main element, and everything in markdown gets wrapped by a parapgraph
*/
main p > img {
  display: block;
  width: 100%;

  /* Inline class style for the img to make it show actual size and centered */
  &.actual-size {
    width: auto;
    margin: auto;
  }
}

/**
 * Allows styling paragraph text like a caption in markdown.
 */
p.caption {
  @include text-size("caption");
  text-align: center;
  margin-top: $space-sm;
}
