// -----------------------------------------------------------------------------
// This file contains color variables for the default theme. Any colors used
// to set a style should be defined as a variable here, rather than defining a
// raw color value, or referencing the base color map defined in _variables.scss.
//
// These color variables live outside of _variables.scss because they rely on
// color functions defined in _functions.scss which are imported afterwards.
// -----------------------------------------------------------------------------

// -----------------------------------------------------------------------------
// Black, white, and grays
// -----------------------------------------------------------------------------

/// Black
/// @type Color
$black: get-color("black") !default;

/// White
/// @type Color
$white: get-color("white") !default;

/// Dark gray
/// @type Color
$gray-dark: get-color-level("black", 1) !default;

/// Medium gray
/// @type Color
$gray-medium: get-color-level("black", 2) !default;

/// Light gray
/// @type Color
$gray-light: get-color-level("black", 3) !default;

/// Lightest gray
/// @type Color
$gray-lightest: get-color-level("black", 4) !default;

// -----------------------------------------------------------------------------
// Semantic color variables
// -----------------------------------------------------------------------------

/// Copy text color (default light background)
/// @type Color
$text-color: $black !default;

/// Copy text color on a dark background
/// @type Color
$text-color-inverted: $white !default;

/// Link color
/// @type Color
$link-color: get-color("anu-gold") !default;

/// Primary brand color
/// @type Color
$primary-color: get-color("anu-gold") !default;

/// Light version of primary brand color for backgrounds
/// @type Color
$primary-bg-color: get-color-level("anu-gold", 4) !default;

/// Secondary brand color
/// @type Color
$secondary-color: get-color("copper") !default;

/// Tertiary brand color
/// @type Color
$tertiary-color: get-color("teal") !default;

/// Component border color
/// @type Color
$component-border-color: $gray-light !default;

/// Component background color
/// @type Color
$component-bg-color: $gray-lightest !default;

/// Component disabled bg color
/// @type Color
$component-disabled-bg-color: $gray-lightest !default;

/// Component disabled text color
/// @type Color
$component-disabled-text-color: $gray-medium !default;

/// Component text color
/// @type Color
$component-text-color: $gray-dark !default;

/// Error color
/// @type Color
$error-color: get-color("copper") !default;

/// Error background color
/// @type Color
$error-bg-color: get-color-alpha("copper", "lightest") !default;

/// Warning color
/// @type Color
$warn-color: get-color("anu-gold") !default;

/// Warning background color
/// @type Color
$warn-bg-color: get-color-alpha("anu-gold", "lightest") !default;

/// Info color
/// @type Color
$info-color: get-color("teal") !default;

/// Info background color
/// @type Color
$info-bg-color: get-color-alpha("teal", "lightest") !default;

/// Contrast section background
/// @type Color
$contrast-section-bg-color: $primary-bg-color !default;

/// Contrast section text color
/// @type Color
$contrast-section-text-color: $text-color !default;

/// Contrast hover color (for cards on contrast sections)
/// @type Color
$contrast-section-hover-color: get-color("white") !default;

// -----------------------------------------------------------------------------
// Transparent colors
// -----------------------------------------------------------------------------

/// Overlay color (for hiding content behind slide-in menus / modals)
/// @type Color
$overlay-color: get-color-alpha("black", "dark") !default;

/// Image overlay color
/// @type Color
$image-overlay-color: get-color-alpha("black", "light") !default;

/// Box shadow colour
/// @type Color
$box-shadow-color: get-color-alpha("black", "lightest") !default;

/// Control indicator color e.g. select dropdown caret
/// @type Color
$control-indicator-color: get-color-alpha("black", "medium") !default;

/// Banner caption color
/// @type Color
$banner-caption-color: get-color-alpha("white", "dark") !default;

// -----------------------------------------------------------------------------
// Expose basic theme colours as CSS variables, for use in HTML only.
// Please use Sass variables provided above when writing stylesheets.
// -----------------------------------------------------------------------------

:root {
  --color-gray-dark: #{$gray-dark};
  --color-gray-medium: #{$gray-medium};
  --color-gray-light: #{$gray-light};
  --color-gray-lightest: #{$gray-lightest};
  --color-gold: #{get-color("anu-gold")};
  --color-gold-tint: #{get-color-level("anu-gold", 4)};
  --color-copper: #{get-color("copper")};
  --color-teal: #{get-color("teal")};
}
