// -----------------------------------------------------------------------------
// This file contains all styles relating to slide layouts
// -----------------------------------------------------------------------------

/* prettier-ignore */
$reveal-slide-bg-color: map-get($reveal-bg-color, $reveal-slide-theme);
/* prettier-ignore */
$reveal-slide-body-color: map-get($reveal-body-color, $reveal-slide-theme);
/* prettier-ignore */
$reveal-slide-headline-color: map-get($reveal-headline-color, $reveal-slide-theme);

.reveal .slides > section {
  color: $reveal-slide-body-color;

  /**
  * Reveal calculates & adds .has-dark-background & .has-light-background
  * classes automatically. Override the text color for these classes in case
  * it's because the user has manually set data-background on a slide.
  */
  &.has-dark-background {
    color: map-get($reveal-body-color, "dark");
  }

  &.has-light-background {
    color: map-get($reveal-body-color, "light");
  }

  /**
  * Default slide layout (if it doesn't have any layout classes on it
  */
  grid-template-areas:
    "hdr hdr hdr gap art art art art art art art art"
    "asd asd asd gap art art art art art art art art"
    "spc spc spc gap art art art art art art art art";

  > header {
    grid-area: hdr;

    /* Only do the first heading (e.g. if there's both h1 & h2 on the slide) */
    h1:first-child,
    h2:first-child {
      color: $reveal-slide-headline-color;
    }
  }

  > article {
    grid-area: art;
  }

  > aside {
    grid-area: asd;
  }

  /**
  * Narrow slide layout
  */
  &.narrow {
    grid-template-areas:
      "lsp lsp hdr hdr hdr hdr hdr hdr hdr hdr rsp rsp"
      "lsp lsp art art art art art art art art rsp rsp"
      "lsp lsp asd asd asd asd asd asd asd asd rsp rsp";
  }
}

.reveal .backgrounds > .slide-background {
  background-color: $reveal-slide-bg-color;
}
